.login-page {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.login-button {
  font-size: 1.4rem;
  padding: 1.4rem;
  border: none;
  border-radius: 8px;
  color: #fff;
  background-color: #c51162;
}

.login-button:hover {
  cursor: pointer;
  background-color: #fff;
  color: #c51162;
}
