.sidebar {
  background-color: black;
  color: #b3b3b3;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 200px;
}
.sidebar-closed {
  transition: width 0.3s ease-in-out;
  width: 60px;
}
.menu-toggle {
  color: #b2bac2;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding-left: 20px;
  margin-left: auto;
  margin-right: 10px;
}
.menu-toggle-closed {
  padding: 0;
  margin: 0;
}
.top-links {
  display: flex;
  flex-direction: column;
  margin: 0;
}
.logo {
  text-align: center;
  margin: 1rem 0;
}
.logo-img {
  width: 80px;
  height: auto;
}
.logo-img-closed {
  width: 50px;
  height: auto;
}

.nav-link {
  text-decoration: none;
}
.nav-link:hover {
  cursor: pointer;
  color: white;
}
ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 20px;
}
li {
  display: flex;
  transition: 0.3s ease-in-out;
}
.display-none {
  display: none;
}
.margin-left {
  margin-left: 20px;
}
